import { Injectable } from '@angular/core';
import {ItemsModule} from './items.module';
import { Ingredient } from '../shared/ingredient.model';
import { ShoppingListService } from '../shopping-list/shopping-list.service';


@Injectable({
  providedIn: 'root'
})
export class ItemsService {

  private items: ItemsModule[]= [
    new ItemsModule(
      'Starcraft2', 
      'RTS Game', 
      'https://www.geek.com/wp-content/uploads/2010/07/starcraft2-625x352.jpg',
      [new Ingredient('test', 1), 
      new Ingredient('test', 2)]),
    new ItemsModule(
      'Zerg Queen', 
      'Zerg unit', 
      'https://swarmscblog.files.wordpress.com/2017/11/queen_eggs.jpg',
      [new Ingredient('test', 3), 
      new Ingredient('test', 4)])
  ];

  constructor(private slService : ShoppingListService) { }

  getItems() {
    return this.items.slice();
  }
  getItem(id: number){
    return this.items[id];
  }

  addIngredientsToShoppingList(ingredients: Ingredient[]) {
    this.slService.addIngredients(ingredients);
  }

 
}
